* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  list-style: none;
}

body {
  margin: 0;
  font-family: "Merriweather", serif;
  background-color: #f4f4f4;
}

.container {
  width: 80%;
  margin: auto;
  overflow: hidden;
}

/* logo animation */
@media (prefers-reduced-motion: no-preference) {
  .logo {
    animation: logo-spin infinite 10s linear;
  }
}

@keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo {
  background-color: #fff;
  height: 50px;
  width: 50px;
  border-radius: 100%;
}
/* nav */
header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  position: fixed;
  background-color: rgba(0, 136, 255, 0.2);
  height: 60px;
  display: flex;
  align-items: center;
}

a {
  color: #fff;
  text-decoration: none;
}

.head {
  display: flex;
  justify-content: space-between;
}

aside {
  width: 40%;
  margin-right: 20px;
}

.nav {
  display: flex;
  color: #fff;
  justify-content: space-between;
  height: 50px;
  align-items: center;
}

.nav > div {
  cursor: pointer;
}

.harmburger {
  display: none;
}

.close {
  display: none;
}

/* Homepage style */
.showcase img {
  height: 100vh;
  width: 100%;
}

.upper {
  display: flex;
  justify-content: center;
  text-align: center;
  background: linear-gradient(rgba(24, 136, 255, 0.2), rgba(0, 136, 255, 0.2));
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}

span img {
  height: 50px;
  width: 50px;
}

h1 {
  font-size: 562.5%;
  font-family: "Sacramento", cursive;
  color: #fff;
}

h2 {
  font-size: 3rem;
  color: #fff;
  font-weight: normal;
  line-height: 3;
  font-family: "Montserrat", sans-serif;
  margin-bottom: 40px;
}

:root {
  --primary: #1888ff;
}

button {
  padding: 8px 20px;
  border-radius: 4px;
  outline: none;
  border: none;
  font-size: 18px;
  color: #fff;
  cursor: pointer;
  background-color: var(--primary);
  font-family: "Montserrat", sans-serif;
}

button:hover {
  padding: 6px 18px;
  transition: all 0.3s ease-out;
  background-color: transparent;
  color: #fff;
  border-radius: 4px;
  border: 2px solid var(--primary);
}

.dev {
  text-decoration: underline;
}

/* portfolio style */

.projects {
  height: 500px;
  padding-top: 50px;
  padding-bottom: 50px;
}
.image-one {
  height: 250px;
  width: 250px;
}

.project-list {
  display: flex !important;
  justify-content: space-between !important;
  align-items: center;
  padding-bottom: 20px;
}

.project {
  height: 255px;
  width: 250px;
}

.container-pro {
  width: 90%;
  margin: auto;
  overflow: hidden;
}

.port-head {
  font-size: 562.5%;
  text-align: center;
  color: #1888ff;
  font-family: "Montserrat", sans-serif;
  padding-bottom: 30px;
}

/* about */

.profile {
  background: linear-gradient(rgba(24, 136, 255, 0.5), rgba(0, 136, 255, 0.5)),
    url("./img/pexels-mati-mango-5952645.jpg");
  height: 700px;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  justify-content: center;
  text-align: inherit;
  color: #fff;
}

.about {
  font-size: 562.5%;
  font-family: "Montserrat", sans-serif;
  line-height: 3;
  text-align: center;
}

.profile p {
  line-height: 2;
  font-size: 20px;
  line-height: 2.5;
}

/* contact */

ul li {
  list-style: none;
}

ul {
  display: flex;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
}

li {
  margin: 5px;
  border-right: 2px #fff solid;
  padding-right: 10px;
}

.last-media {
  border: none !important;
}

.contact {
  background-color: #1888ff;
  height: auto;
  display: flex;
  justify-content: center;
  color: #fff;
  padding: 10px 0;
}

.contact-ok {
  font-size: 562.5%;
  font-family: "Montserrat", sans-serif;
  text-align: inherit;
}

.contact-des {
  font-size: 40px;
  padding: 20px;
  line-height: 1.5;
  text-align: inherit;
}

.get-intouch {
  border: 2px #fff solid;
  padding: 20px;
  font-size: 2rem;
}

.get {
  padding: 30px;
  text-align: center;
}

.fa-paper-plane {
  padding-right: 40px;
  font-size: 3rem;
}

/* footer */

footer {
  height: 300px;
  background-color: #36454f;
  display: flex;
  justify-content: center;
  text-align: center;
}

.social {
  width: 50%;
  display: flex;
  justify-content: space-between;
  line-height: 2;
  text-align: center;
}

.made {
  font-size: 30px;
  line-height: 2;
  padding: 10px;
}

.twitter {
  background-color: #000;
  color: #36454f;
  padding: 10px;
}

.link {
  background-color: #000;
  padding: 10px;
}

span a {
  color: #36454f;
}

.tool p {
  line-height: 2;
}
.tools {
  padding: 20px;
}

.social-align {
  display: flex;
  justify-content: center;
}

.name {
  color: #1888ff;
}

.fa-twitter {
  color: #1888ff;
  padding-right: 5px;
}

.fa-linkedin {
  color: #0072b1;
  padding-right: 5px;
}

